  margin-bottom: .5rem;\n}\n\nbutton {\n  border-radius: 0;\n}\n\nbutton:focus {\n  outline: 1px dotted;\n  outline: 5px auto -webkit-focus-ring-color;\n}\n\ninput,\nbutton,\nselect,\noptgroup,\ntextarea {\n  margin: 0;\n  font-family: inherit;\n  font-size: inherit;\n  line-height: inherit;\n}\n\nbutton,\ninput {\n  overflow: visible;\n}\n\nbutton,\nselect {\n  text-transform: none;\n}\n\nbutton,\nhtml [type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button;\n}\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  padding: 0;\n  border-style: none;\n}\n\ninput[type=\"radio\"],\ninput[type=\"checkbox\"] {\n  box-sizing: border-box;\n  padding: 0;\n}\n\ninput[type=\"date\"],\ninput[type=\"time\"],\ninput[type=\"datetime-local\"],\ninput[type=\"month\"] {\n  -webkit-appearance: listbox;\n}\n\ntextarea {\n  overflow: auto;\n  resize: vertical;\n}\n\nfieldset {\n  min-width: 0;\n  padding: 0;\n  margin: 0;\n  border: 0;\n}\n\nlegend {\n  display: block;\n  width: 100%;\n  max-width: 100%;\n  padding: 0;\n  margin-bottom: .5rem;\n  font-size: 1.5rem;\n  line-height: inherit;\n  color: inherit;\n  white-space: normal;\n}\n\nprogress {\n  vertical-align: baseline;\n}\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n[type=\"search\"] {\n  outline-offset: -2px;\n  -webkit-appearance: none;\n}\n\n[type=\"search\"]::-webkit-search-cancel-button,\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n::-webkit-file-upload-button {\n  font: inherit;\n  -webkit-appearance: button;\n}\n\noutput {\n  display: inline-block;\n}\n\nsummary {\n  display: list-item;\n}\n\ntemplate {\n  display: none;\n}\n\n[hidden] {\n  display: none !important;\n}\n","$theme-bg-color: #f1f4f6;\n$theme-focus: #444054;\n$theme-alternate: #794c8a;\n$theme-breakpoint-xs: 0;\n$theme-breakpoint-sm: 576px;\n$theme-breakpoint-md: 768px;\n$theme-breakpoint-lg: 992px;\n$theme-breakpoint-xl: 1200px;\n$theme-border-radius: .25rem;\n$theme-box-shadow: 0 0.46875rem 2.1875rem rgba(4, 9, 20, 0.03),\n    0 0.9375rem 1.40625rem rgba(4, 9, 20, 0.03),\n    0 0.25rem 0.53125rem rgba(4, 9, 20, 0.05),\n    0 0.125rem 0.1875rem rgba(4, 9, 20, 0.03);\n\n\n\n:root {\n    /* Theme */\n    --theme-bg-color: #{$theme-bg-color};\n    --theme-blue: #{$theme-color-blue};\n    --theme-indigo: #{$theme-color-indigo};\n    --theme-purple: #{$theme-color-purple};\n    --theme-pink: #{$theme-color-pink};\n    --theme-red: #{$theme-color-red};\n    --theme-orange: #{$theme-color-orange};\n    --theme-yellow: #{$theme-color-yellow};\n    --theme-green: #{$theme-color-green};\n    --theme-teal: #{$theme-color-teal};\n    --theme-cyan: #{$theme-color-cyan};\n    --theme-white: #{$theme-color-white};\n    --theme-gray: #{$theme-color-gray};\n    --theme-gray-dark: #{$theme-color-gray-dark};\n    --theme-primary: #{$theme-color-primary};\n    --theme-secondary: #{$theme-color-secondary};\n    --theme-success: #{$theme-color-success};\n    --theme-info: #{$theme-color-info};\n    --theme-warning: #{$theme-color-warning};\n    --theme-danger: #{$theme-color-danger};\n    --theme-light: #{$theme-color-light};\n    --theme-dark: #{$theme-color-dark};\n    --theme-focus: #{$theme-focus};\n    --theme-alternate: #{$theme-alternate};\n    --theme-breakpoint-xs: #{$theme-breakpoint-xs};\n    --theme-breakpoint-sm: #{$theme-breakpoint-sm};\n    --theme-breakpoint-md: #{$theme-breakpoint-md};\n    --theme-breakpoint-lg: #{$theme-breakpoint-lg};\n    --theme-breakpoint-xl: #{$theme-breakpoint-xl};\n    --theme-border-radius: #{$theme-border-radius};\n    --theme-box-shadow: #{$theme-box-shadow};\n\n\n    /* Text */\n    --global-text-color-light: #fff;\n    --global-text-color-dark: #495057;\n\n    /* Border */\n    --global-border-color-light: #fff;\n    --global-border-color-dark: #000;\n\n    /* Background Colors */\n    --global-bg-color-default: #fff;\n    --global-bg-color-primary: var(--theme-primary);\n    --global-bg-color-secondary: var(--theme-secondary);\n    --global-bg-color-success: var(--theme-success);\n    --global-bg-color-danger: var(--theme-danger);\n    --global-bg-color-warning: var(--theme-warning);\n    --global-bg-color-info: var(--theme-info);\n    --global-bg-color-default-hover: scale-color(#fff, $lightness: -15%);\n    // --global-bg-color-primary-hover: scale-color(var(--theme-primary), $lightness: -15%);\n    --global-bg-color-primary-hover: scale-color(#E0E3E4, $lightness: 5%);\n    --global-bg-color-secondary-hover: scale-color(var(--theme-secondary), $lightness: -15%);\n    --global-bg-color-success-hover: scale-color(var(--theme-success), $lightness: -15%);\n    --global-bg-color-danger-hover: scale-color(var(--theme-danger), $lightness: -15%);\n    --global-bg-color-warning-hover: scale-color(var(--theme-warning), $lightness: -15%);\n    --global-bg-color-info-hover: scale-color(var(--theme-info), $lightness: -15%);\n\n    /* Layouts */\n    --layout-aside-width: 250px;\n    --layout-radius: 4px;\n    --layout-bg-color: #f1f4f6;\n    --layout-aside-bg-color: #ffffff;\n\n    /* Shadow */\n    --border-shadow: 0 1px 15px rgba(0, 0, 0, 0.04), 0 1px 6px rgba(0, 0, 0, 0.04);\n\n    /* Navbar */\n    --navbar-height: 60px;\n    --navbar-title-width: var(--layout-aside-width);\n    --navbar-bg-color: #fafbfc;\n    --navbar-box-shadow: var(--theme-box-shadow);\n\n    /* Card */\n    --card-padding-width: 1.25rem;\n    --card-box-shadow: var(--theme-box-shadow);\n}\n",".w {\n  @for $var from 1 to 13 {\n    &-#{$var} {\n      width: ($var / 4) * 1rem;\n    }\n  }\n}\n\n.h {\n  &-full {\n    height: 100%;\n  }\n}\n",".cursor-pointer {\n  cursor: pointer;\n}",".flex {\n    display: flex;\n\n    &-1 {\n        flex-grow: 1;\n    }\n\n    &-wrap {\n        flex-wrap: wrap;\n    }\n\n    &-container {\n        flex-wrap: nowrap;\n    }\n}\n\n\n/* RESPONSIVE GRID */\n$items: 12;\n$breakpoints: ('xs': 1px,\n    'sm': 1px,\n    'md': 1px,\n    'lg': 1px,\n    'xl': 1px);\n\n\n\n.row {\n    display: flex;\n    flex-wrap: wrap;\n    width: 100%;\n}\n\n.col {\n    display: flex;\n    flex-direction: column;\n    flex-grow: 0;\n    flex-shrink: 0;\n    min-height: 1px;\n}\n\n/* Extra small devices (phones, 600px and down) */\n@mixin break-xs() {\n    @media (max-width: 575px) {\n        @content;\n    }\n}\n\n/* Small devices (portrait tablets and large phones, 600px and up) */\n@mixin break-sm() {\n    @media (min-width: 576px) {\n        @content;\n    }\n}\n\n/* Medium devices (landscape tablets, 768px and up) */\n@mixin break-md() {\n    @media (min-width: 768px) {\n        @content;\n    }\n}\n\n/* Large devices (laptops/desktops, 992px and up) */\n@mixin break-lg() {\n    @media (min-width: 992px) {\n        @content;\n    }\n}\n\n/* Extra large devices (large laptops and desktops, 1200px and up) */\n@mixin break-xl() {\n    @media (min-width: 1200px) {\n        @content;\n    }\n}\n\n.row > [class^=col-] {\n    width: 100%;\n}\n\n.col {\n    @for $i from 1 through $items {\n        &-xs-#{$i} {\n            @include break-xs() {\n                -webkit-box-flex: 0;\n                flex: 0 0 #{$i / $items * 100%};\n                max-width: #{$i / $items * 100%};\n            }\n        }\n        &-sm-#{$i} {\n            @include break-sm() {\n                -webkit-box-flex: 0;\n                flex: 0 0 #{$i / $items * 100%};\n                max-width: #{$i / $items * 100%};\n            }\n        }\n        &-md-#{$i} {\n            @include break-md() {\n                -webkit-box-flex: 0;\n                flex: 0 0 #{$i / $items * 100%};\n                max-width: #{$i / $items * 100%};\n            }\n        }\n        &-lg-#{$i} {\n            @include break-lg() {\n                -webkit-box-flex: 0;\n                flex: 0 0 #{$i / $items * 100%};\n                max-width: #{$i / $items * 100%};\n            }\n        }\n        &-xl-#{$i} {\n            @include break-xl() {\n                -webkit-box-flex: 0;\n                flex: 0 0 #{$i / $items * 100%};\n                max-width: #{$i / $items * 100%};\n            }\n        }\n    }\n}\n\n\n@include break-xs() {\n    .xs-hidden {\n        display: none;\n    }\n}\n@include break-sm() {\n    .sm-hidden {\n        display: none;\n    }\n}\n@include break-md() {\n    .md-hidden {\n        display: none;\n    }\n}\n@include break-lg() {\n    .lg-hidden {\n        display: none;\n    }\n}\n@include break-xl() {\n    .xl-hidden {\n        display: none;\n    }\n}\n","$spaces-divisor: 4;\n\n// Margins\n.m {   \n    @for $var from 0 to 10 {\n    \n        &-#{$var} {\n            margin: ($var / $spaces-divisor) * 1rem;\n        }\n        \n        &t-#{$var} {\n            margin-top: ($var / $spaces-divisor) * 1rem;\n        }\n        \n        &r-#{$var} {\n            margin-right: ($var / $spaces-divisor) * 1rem;\n        }\n        \n        &b-#{$var} {\n            margin-bottom: ($var / $spaces-divisor) * 1rem;\n        }\n        \n        &l-#{$var} {\n            margin-left: ($var / $spaces-divisor) * 1rem;\n        }\n        \n        &y-#{$var} {\n            margin-top: ($var / $spaces-divisor) * 1rem;\n            margin-bottom: ($var / $spaces-divisor) * 1rem;\n        }\n        \n        &x-#{$var} {\n            margin-left: ($var / $spaces-divisor) * 1rem;\n            margin-right: ($var / $spaces-divisor) * 1rem;\n        }\n    }\n}\n\n// Paddings\n.p {\n    @for $var from 0 to 10 {\n\n        &-#{$var} {\n            padding: ($var / $spaces-divisor) * 1rem;\n        }\n\n        &t-#{$var} {\n            padding-top: ($var / $spaces-divisor) * 1rem;\n        }\n\n        &r-#{$var} {\n            padding-right: ($var / $spaces-divisor) * 1rem;\n        }\n\n        &b-#{$var} {\n            padding-bottom: ($var / $spaces-divisor) * 1rem;\n        }\n\n        &l-#{$var} {\n            padding-left: ($var / $spaces-divisor) * 1rem;\n        }\n\n        &y-#{$var} {\n            padding-top: ($var / $spaces-divisor) * 1rem;\n            padding-bottom: ($var / $spaces-divisor) * 1rem;\n        }\n\n        &x-#{$var} {\n            padding-left: ($var / $spaces-divisor) * 1rem;\n            padding-right: ($var / $spaces-divisor) * 1rem;\n        }\n    }\n}\n",".text {\n  &-center {\n    text-align: center;\n  }\n\n  &-left {\n    text-align: left;\n  }\n\n  &-right {\n    text-align: right;\n  }\n\n  @each $var,\n  $val in $colors {\n    &-#{$var} {\n      color: $val;\n    }\n  }\n\n  &-noselection {\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n  }\n}\n\n.uppercase {\n  text-transform: uppercase;\n}\n","// Colors\n$theme-color-blue: #007bff;\n$theme-color-indigo: #6610f2;\n$theme-color-purple: #6f42c1;\n$theme-color-pink: #e83e8c;\n$theme-color-red: #dc3545;\n$theme-color-orange: #fd7e14;\n$theme-color-yellow: #ffc107;\n$theme-color-green: #28a745;\n$theme-color-teal: #20c997;\n$theme-color-cyan: #17a2b8;\n$theme-color-white: #fff;\n$theme-color-gray: #6c757d;\n$theme-color-gray-dark: #343a40;\n$theme-color-primary: #3f6ad8;\n$theme-color-secondary: #6c757d;\n$theme-color-success: #3ac47d;\n$theme-color-info: #16aaff;\n$theme-color-warning: #f7b924;\n$theme-color-danger: #d92550;\n$theme-color-light: #eeeeee;\n$theme-color-dark: #343a40;\n\n$colors: (\n    \"blue\": $theme-color-blue,\n    \"indigo\": $theme-color-indigo,\n    \"purple\": $theme-color-purple,\n    \"pink\": $theme-color-pink,\n    \"red\": $theme-color-red,\n    \"orange\": $theme-color-orange,\n    \"yellow\": $theme-color-yellow,\n    \"green\": $theme-color-green,\n    \"teal\": $theme-color-teal,\n    \"cyan\": $theme-color-cyan,\n    \"white\": $theme-color-white,\n    \"gray\": $theme-color-gray,\n    \"gray-dark\": $theme-color-gray-dark,\n    \"primary\": $theme-col