ound-color: scale-color($theme-color-primary, $lightness: -15%);\n    }\n  }\n\n  &-secondary {\n    box-shadow: 0 0.125rem 0.625rem rgba(108, 117, 125, 0.4), 0 0.0625rem 0.125rem rgba(108, 117, 125, 0.5);\n    background-color: var(--global-bg-color-secondary);\n    color: var(--global-text-color-light);\n    border-color: transparent;\n\n    &:hover {\n      background-color: scale-color($theme-color-secondary, $lightness: -15%);\n    }\n  }\n\n  &-success {\n    box-shadow: 0 0.125rem 0.625rem rgba(58, 196, 125, 0.4), 0 0.0625rem 0.125rem rgba(58, 196, 125, 0.5);\n    background-color: var(--global-bg-color-success);\n    color: var(--global-text-color-light);\n    border-color: transparent;\n\n    &:hover {\n      background-color: scale-color($theme-color-success, $lightness: -15%);\n    }\n  }\n\n  &-info {\n    box-shadow: 0 0.125rem 0.625rem rgba(22, 170, 255, 0.4), 0 0.0625rem 0.125rem rgba(22, 170, 255, 0.5);\n    background-color: var(--global-bg-color-info);\n    color: var(--global-text-color-light);\n    border-color: transparent;\n\n    &:hover {\n      background-color: scale-color($theme-color-info, $lightness: -15%);\n    }\n  }\n\n  &-danger {\n    box-shadow: 0 0.125rem 0.625rem rgba(217, 37, 80, 0.4), 0 0.0625rem 0.125rem rgba(217, 37, 80, 0.5);\n    background-color: var(--global-bg-color-danger);\n    color: var(--global-text-color-light);\n    border-color: transparent;\n\n    &:hover {\n      background-color: scale-color($theme-color-danger, $lightness: -15%);\n    }\n  }\n\n  &-warning {\n    box-shadow: 0 0.125rem 0.625rem rgba(247, 185, 36, 0.4), 0 0.0625rem 0.125rem rgba(247, 185, 36, 0.5);\n    background-color: var(--global-bg-color-warning);\n    color: var(--global-text-color-light);\n    border-color: transparent;\n\n    &:hover {\n      background-color: scale-color($theme-color-warning, $lightness: -15%);\n    }\n  }\n\n  &-noshadow {\n    box-shadow: none;\n  }\n}\n\n\n/* Button Group */\n.v-button-group {\n  .v-button {\n    bo